import React from 'react';
import PropTypes from 'prop-types';
import { Helmet, HelmetProvider } from 'react-helmet-async';

import Analytics from '../components/Template/Analytics';
import Navigation from '../components/Template/Navigation';
import SideBar from '../components/Template/SideBar';
import Intro from '../components/Template/Intro';
import Outro from '../components/Template/Outro';
import ScrollToTop from '../components/Template/ScrollToTop';

const Main = (props) => (
	<HelmetProvider>
		<Analytics />
		<ScrollToTop />
		<Helmet
			titleTemplate="%s | Vadim Isakov"
			defaultTitle="Vadim Isakov"
			defer={false}
		>
			{props.title && <title>{props.title}</title>}
			<meta name="description" content={props.description} />
		</Helmet>
		<div id="wrapper">
			<Navigation />
			{!props.fullPage && <Intro />}
			<div id="main">{props.children}</div>
			{!props.fullPage && <Outro />}
			{!props.fullPage && <SideBar />}
		</div>
	</HelmetProvider>
);

Main.propTypes = {
	children: PropTypes.oneOfType([
		PropTypes.arrayOf(PropTypes.node),
		PropTypes.node,
	]),
	fullPage: PropTypes.bool,
	title: PropTypes.string,
	description: PropTypes.string,
};

Main.defaultProps = {
	children: null,
	fullPage: false,
	title: null,
	description: "Vadim Isakov's personal website.",
};

export default Main;
